.App {
  .App-header {
    font-family: 'Lobster', cursive;
  }
  .App-profile {
    text-align: center;
    background-color: #282c34;
    img {
      max-width: 300px;
    }
  }
  .App-content {
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
  }
}
.width {
  max-width: 900px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}
@media (min-width: 920px) {
  .width {
    padding-left: 0px;
    padding-right: 0px;
  }
}